import { moment, weeks, times } from '../utils/moment'
export default {
  data () {
    return {
      scheduleData: times,
      weeks
    }
  },
  methods: {
    TPT () {
      let t = new Date(Date.now() - 10 * 86400000)
      return this.MFD(t)
    },
    TFT () {
      let t = new Date(Date.now() + 10 * 86400000)
      return this.MFD(t)
    },
    GT () {
      let t = new Date(Date.now())
      return this.MFD(t)
    },
    RD (a, b) {
      b = this.$lib.myMoment(b || Date.now()).formate('YYYY-MM-DD')
      return new Date(b) > new Date(a)
    },
    GABC () {
      let s = ''; let e = ''
      for (const c of this.allClassData) {
        let d = new Date(c.date).getTime()
        if (s) { s = s > d ? d : s } else { s = d }
        if (e) { e = e < d ? d : e } else { e = d }
      }
      return { s: this.MFD(new Date(s)), e: this.MFD(new Date(e)) }
    },
    FNT (time) {
      let index = times.indexOf(time)
      if (index >= 0 && index < times.length - 1) {
        return times[index + 1]
      }
      return '23:30'
    },
    MF (date) {
      return this.MFD(moment.getDateMonthFirst(date))
    },
    ML (date) {
      return this.MFD(moment.getDateMonthLast(date))
    },
    MFD (date) {
      return moment.myMoment(date).formate('YYYY-MM-DD')
    },
    GA (s, e) {
      s = this.MFD(s)
      e = this.MFD(e)
      return moment.getAllDates(s, e)
    },
    GW (d) {
      return new Date(d).getDay()
    }
  }
}
