
<template>
  <div class="home-container hw-100">
    <Layout class="hw-100">
      <Header class="home-header">
        <h1 @click="menuSelect('/')" class="title">排课统计管理系统</h1>
        <Menu mode="horizontal" theme="primary" :active-name="$route.path" @on-select="menuSelect">
          <div class="menu-list" v-for="menu in menuList" :key="menu.value">
            <MenuItem :name="menu.value">
              <Icon :size="24" :type="menu.icon" />
              <span class="first-label">{{menu.label}}</span>
            </MenuItem>
          </div>
        </Menu>
      </Header>
      <Layout>
        <Sider class="left-sider" v-show="subMenu.length>0" hide-trigger :style="{background: '#fff'}">
          <Menu width="auto" mode="vertical" :active-name="$route.path" @on-select="subMenuSelect">
            <MenuItem v-for="menu in subMenu" :key="menu.value" :name="menu.value">
              <Icon :size="24" :type="menu.icon" />
              <span class="first-label">{{menu.label}}</span>
            </MenuItem>
          </Menu>
        </Sider>
        <Layout style="height:100%;">
          <Content>
            <router-view/>
          </Content>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>
<script>
export default {
  data () {
    return {
      menuList: [
        {
          icon: 'ios-calendar-outline',
          label: '课程日历',
          value: '/calendar'
        }, {
          icon: 'md-grid',
          label: '课程分布',
          value: '/schedule'
        }, {
          icon: 'ios-paper-outline',
          label: '统计分析',
          value: '/statistics/summary',
          children: [{
            icon: 'ios-people-outline',
            label: '课程统计',
            value: '/statistics/summary'
          }]
        }, {
          icon: 'ios-settings-outline',
          label: '系统管理',
          value: '/system/students',
          children: [{
            icon: 'ios-people-outline',
            label: '学生列表',
            value: '/system/students'
          }]
        }
      ],
      subMenu: []
    }
  },
  created () {
    this.initMenu()
  },
  methods: {
    initMenu () {
      let path = this.$route.path
      for (const menu of this.menuList) {
        if (menu.value === path && menu.children) {
          this.subMenu = menu.children
        }
      }
    },
    menuSelect (name) {
      this.subMenu = []
      for (const menu of this.menuList) {
        if (menu.value === name && menu.children) {
          this.subMenu = menu.children
        }
      }
      this.$router.push(name)
    },
    subMenuSelect (name) {
      this.$router.push(name)
    }
  }
}
</script>
