export default {

  data () {
    return {
      students: [],
      studentsMap: {}
    }
  },
  created () {
    this.$indexedDB.initStore('students', this.queryStudents)
    this.$bus.$on('students', this.queryStudents)
  },
  mounted () {},
  methods: {
    getStudentById (id) {
      for (const student of this.students) {
        if (Number(id) === student.id) {
          return student
        }
      }
      this.$Message.info('未查询到改学生')
    },
    queryStudents () {
      this.$indexedDB.http({
        method: 'get',
        url: 'students'
      }).then((res) => {
        this.students = res.data
        for (const student of res.data) {
          if (!student.isDelect) {
            this.$set(this.studentsMap, student.id, student.name)
          }
        }
      })
    }
  }
}
