<template>
  <Modal :width="800"  @on-cancel="addModalCancel" class="add-classes-components" v-model="modalControl" :title="edit?'编辑':'添加'">
    <div class="form">
      <Form ref="addFormData" inline :model="addFormData" :rules="addFormRule" :label-width="90">
        <FormItem label="姓名" prop="studentId">
          <Select filterable :disabled="addFormDataDisable.studentId" style="width: 160px" v-model="addFormData.studentId" placeholder="学生姓名">
            <Option v-for="(label,value) in studentsMap" :key="value" :value="value">{{label}}</Option>
          </Select>
        </FormItem>
        <FormItem label="课时数" prop="count">
          <InputNumber style="width: 160px" :editable="false" :max="10" :min="1" v-model="addFormData.count"></InputNumber>
        </FormItem>
         <FormItem v-if="search" :label-width="20">
          <Button type="primary" @click="searchTable()">搜索</Button>
        </FormItem>
        <FormItem label="日期" prop="date">
          <DatePicker :disabled="addFormDataDisable.date" :clearable="false" v-model="addFormData.date" type="date" placeholder="日期" style="width: 160px"></DatePicker>
        </FormItem>
        <FormItem label="开始时间" prop="startTime">
          <Select style="width: 160px" v-model="addFormData.startTime" placeholder="时间">
            <Option v-for="time in dealScheduleData" :key="time" :value="time">{{time}}</Option>
          </Select>
        </FormItem>
        <FormItem :label-width="20">
          <Button type="primary" @click="addTable()">添加</Button>
        </FormItem>
      </Form>
    </div>
    <div class="table">
      <Table height="400" :columns="addFormColumns" :data="dealAddTableData"></Table>
    </div>
    <div slot="footer">
      <Button @click="addModalCancel()" style="margin-right: 10px">取消</Button>
      <Button type="primary" v-if="edit||search" @click="editModalOk()">编辑</Button>
      <Button v-else type="primary" @click="addModalOk()">确定</Button>
    </div>
  </Modal>

</template>

<script>
import { students, dates, classes, subjects } from '../mixins/index.js'
export default {
  mixins: [students, dates, classes, subjects],
  props: {
    search: { type: Boolean, default: false },
    edit: { type: Boolean, default: false },
    modal: { type: Boolean },
    date: { type: String },
    classes: { type: Array },
    studentId: [Number, String]
  },
  data () {
    return {
      modalControl: false,
      searchStudentId: '',
      addFormData: {
        studentId: '',
        startTime: '',
        date: '',
        count: 3
      },
      addFormDataDisable: {
        studentId: false,
        date: false
      },
      addFormRule: {
        studentId: [
          { required: true, message: '请选择学生姓名', trigger: 'change' }
        ],
        date: [
          { required: true, type: 'date', message: '请选择上课日期', trigger: 'change' }
        ],
        startTime: [
          { required: true, message: '请选择开始时间', trigger: 'change' }
        ]
      },
      addFormColumns: [
        {
          title: '日期',
          key: 'date',
          align: 'center'
        }, {
          title: '时间',
          key: 'periods',
          align: 'center',
          width: 140
        }, {
          title: '姓名',
          key: 'studentName',
          align: 'center'
        }, {
          title: '科目',
          key: 'subject',
          align: 'center'
        }, {
          title: '小时数',
          key: 'count',
          align: 'center'
        }, {
          title: '操作',
          key: 'action',
          width: 200,
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  this.delectTableByDate(params)
                }
              }
            }, '删除')
          }
        }
      ],
      addTableData: [],
      delectCache: []
    }
  },
  watch: {
    modal (value) {
      this.modalControl = value
    },
    date (value) {
      this.addFormDataDisable.date = !!value
      this.addFormData.date = value || ''
    },
    classes (value) {
      if (value) {
        this.addTableData = [...value]
      }
    },
    studentId (value) {
      this.addFormDataDisable.studentId = !!value
      this.addFormData.studentId = value || ''
      if (value) {
        this.addTableData = this.allClassData.filter(item => {
          return Number(item.studentId) === Number(value)
        })
      }
    }
  },
  computed: {
    dealAddTableData () {
      return this.dealClassesToShow(this.addTableData)
    },
    dealScheduleData () {
      return this.scheduleData.filter((item, i) => {
        return this.scheduleData.length - i >= this.addFormData.count
      })
    }
  },
  created () {},
  mounted () {},
  methods: {
    addModalOk () {
      if (this.addTableData.length === 0) {
        this.$Message.info('未排课')
        return
      }
      for (const add of this.addTableData) {
        let result = this.hasPeriodOrNot(add, this.allClassData)
        if (!add.id && result) {
          this.$Message.info(`${result}已有安排，请重新选择`)
          return
        }
      }
      this.addTableClasses()
    },
    editModalOk () {
      this.delectCatchClasses()
      setTimeout(() => {
        this.addModalOk()
      }, 50)
    },
    async delectCatchClasses () {
      let delectCount = 0
      for (const id of this.delectCache) {
        let data = await this.queryClassesByKey(id)
        delectCount += await this.delectClasses(data)
      }
      delectCount > 0 && this.$Message.success(`成功删除了${delectCount}条记录`)
      this.delectCache = []
    },
    async addTableClasses () {
      let count = 0
      for (const add of this.addTableData) {
        if (!add.id) {
          count += await this.addClasses(add)
        }
      }
      this.$Message.success(`成功添加${count}条记录`)
      this.addModalCancel()
    },
    delectTableByDate (params) {
      this.addTableData.splice(params.index, 1)
      if (params.row.id) {
        this.delectCache.push(params.row.id)
      }
    },
    addModalCancel () {
      this.addTableData = []
      this.$refs.addFormData.resetFields()
      this.modalControl = false
      this.$emit('update:modal', this.modalControl)
      this.$emit('update:date', this.addFormData.date)
      this.$emit('update:classes', this.addTableData)
      this.$emit('update:studentId', this.addFormData.studentId)
    },
    searchTable () {
      let { studentId } = this.addFormData
      if (studentId) {
        this.addTableData = this.allClassData.filter(item => {
          return Number(item.studentId) === Number(studentId)
        })
      }
    },
    addTable () {
      this.$refs.addFormData.validate((valid) => {
        if (!valid) return
        let data = { }
        let{ studentId, startTime, date, count } = this.addFormData
        let index = this.scheduleData.indexOf(startTime)
        let student = this.getStudentById(studentId)
        data.periods = this.scheduleData.slice(index, index + count)
        data.date = this.$lib.myMoment(new Date(date)).formate('YYYY-MM-DD')
        let result = this.hasPeriodOrNot(data, this.addTableData)
        if (result) {
          this.$Message.info(`${result}已有安排，请重新选择`)
          return
        }
        data.studentId = studentId
        data.studentName = student.name
        data.subject = student.subject
        this.addTableData.push(data)
      })
    }
  }
}
</script>
