import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      component: Home,
      children: [{
        path: '/',
        name: 'Default',
        component: () => import('./views/Default.vue')
      }, {
        path: '/calendar',
        name: 'Calendar',
        component: () => import('./views/common/Calendar.vue')
      }, {
        path: '/schedule',
        name: 'Schedule',
        component: () => import('./views/common/Schedule.vue')
      }, {
        path: '/system/students',
        name: 'Students',
        component: () => import('./views/system/Students.vue')
      }, {
        path: '/statistics/summary',
        name: 'Summary',
        component: () => import('./views/statistics/Summary.vue')
      }]
    }
  ]
})
