export default {
  data () {
    return {
      allClassData: []
    }
  },
  computed: {
    allTimeClasses () {
      let classes = []
      for (const data of this.allClassData) {
        let { studentName, subject, studentId, periods, date } = data
        for (const period of periods) {
          classes.push({ studentName, subject, studentId, period, date })
        }
      }
      return classes
    }
  },
  created () {
    this.$indexedDB.initStore('curriculum', this.queryClasses)
    this.$bus.$on('curriculum', this.queryClasses)
  },
  methods: {
    classesSort (classes) {
      return classes.sort((x, y) => {
        return new Date(x.date + ' ' + x.periods[0]).getTime() - new Date(y.date + ' ' + y.periods[0]).getTime()
      })
    },
    dealClassesToShow (classes) {
      classes = this.classesSort(classes)
      return classes.map(item => {
        let data = {
          ...item,
          subject: this.subjectMaps[item.subject],
          periods: this.dealPeriods(item.periods),
          count: item.periods.length * 0.5
        }
        return data
      })
    },
    queryClasses () {
      this.$indexedDB.http({
        method: 'get',
        url: 'curriculum'
      }).then((res) => {
        this.allClassData = res.data.filter(item => {
          return !item.isDelect
        })
      })
    },
    queryClassesByKey (key) {
      return this.$indexedDB.http({
        method: 'getKey',
        url: 'curriculum',
        key: key
      }).then((res) => {
        return res.data
      })
    },
    addClasses (data) {
      data.startTime = new Date(data.date + ' ' + data.periods[0]).getTime()
      data.endTime = new Date(data.date + ' ' + data.periods[data.periods.length - 1]).getTime()
      return this.$indexedDB.http({
        method: 'add',
        url: 'curriculum',
        data: data
      }).then((res) => {
        return 1
      })
    },
    delectClasses (data) {
      data.isDelect = true
      return this.$indexedDB.http({
        method: 'put',
        url: 'curriculum',
        data: data
      }).then((res) => {
        return 1
      })
    },
    hasPeriodOrNot (data, classes) {
      for (const c of classes) {
        let result = this.$lib.getDiff(data.periods, c.periods)
        if (data.date === c.date && result) {
          return `${c.date} ${result}`
        }
      }
      return false
    },
    dealPeriods (periods) {
      return `${periods[0]}-${this.FNT(periods[periods.length - 1])}`
    }
  }
}
